.contact {
    background-color: white;
    display: flex;
}

.contactLeft, .contactRight {
    flex: .5;
}

.contactLeft {
    display: flex;
    align-items: center;
    justify-content: center;
}

.contactRight {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.contactRight > form {
    width: 70%;
    height: 70%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

input, textarea {
    width: 300px;
    font-size: 16px;
}

input {
    height: 30px;
}

textarea {
    height: 100px;
}

button {
    width: 100px;
    height: 40px;
    color: white;
    background-color: green;
    border: none;
    border-radius: 10px;
    font-weight: bold;
    margin-top: 20px;
}

button:focus {
    outline: none;
}

button:hover {
    background-color: darkgreen;
}

.contactRight > h2 {
    font-size: 30px;
}

.red {
    color: red;
}

.green {
    color: green;
}

.red, .green {
    font-weight: bold;
    margin-top: 10px;
}

@media only screen and (max-width: 768px) {
    .contact {
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .contactLeft {
        overflow:hidden;
        flex: .2;
    }
    .contactRight {
        flex-direction: column;
        flex: .8;
        justify-content: space-evenly;
    }
    label, input, textarea {
        font-size: 12px;
    }
    input, textarea {
        width: 150px;
    }
    label{ 
        display: none;
    }

}
